import React from 'react';
import {Link} from 'react-router-dom';
import {Grid, Row, Col, Thumbnail} from 'react-bootstrap';

import coaches from './coaches.json';

const CoachTeaser = ({coach}) => (
  <Col md={4}>
    <Thumbnail className="coach-teaser" href={`/#/coaches/${coach.id}`} alt="Coach" src={`./img/coaches/coach${coach.id}.jpg`}>
      <h3>{coach.name}</h3>
      <blockquote>
        <p>{coach.teaser}</p>
        <footer className="author">{coach.author}</footer>
      </blockquote>
    </Thumbnail>
  </Col>
);

const CoachTeasers = ({titled, limit}) => {
  return (
    <div className="panel-even">
      <Grid>
        <Row className="clearfix">
          {titled && <h2 className="uppercase title-underline">Đội ngũ giảng viên</h2>}
        </Row>

        <Row className="clearfix">
          {coaches.slice(0, 3).map(c =>
            <CoachTeaser key={c.id} coach={c} />
          )}
        </Row>

        <Row className="clearfix">
          {coaches.slice(3, 6).map(c =>
            <CoachTeaser key={c.id} coach={c} />
          )}
        </Row>

        <Row className="clearfix">
          {limit > 0 && limit < coaches.length &&
            <p className="uppercase more"><Link to="/coaches">Xem thêm...</Link></p>
          }
        </Row>
      </Grid>
    </div>
  );
};

export default CoachTeasers;



