import React from 'react';
import {Grid, Row, Carousel} from 'react-bootstrap';

import SlideImage1 from './img/slider/Home-Slide1.jpg';
import SlideImage1_1 from './img/slider/Home-Slide1-1.jpg';
import SlideImage2 from './img/slider/Home-Slide2.jpg';
import SlideImage3 from './img/slider/Home-Slide3.jpg';
import SlideImage4 from './img/slider/Home-Slide4.jpg';
import Pdi from './img/pdi.svg';

const IntroSlider = () => {
  return (
    <Grid fluid={true}>
      <Row id="intro-slider">
        <Carousel interval={3500}>
          <Carousel.Item>
            <img id="slide1" alt="" width="100%" src={SlideImage1} />
            <div className="overlay"><img alt="" width="100%" src={Pdi} /></div>
            <Carousel.Caption>
              <h2>Companion with Your Growth</h2>
              </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img id="slide1" alt="" width="100%" src={SlideImage1_1} />
            <div className="overlay"><img alt="" width="100%" src={Pdi} /></div>
            <Carousel.Caption>
              <h2>Đồng hành trên từng bước trưởng thành</h2>
              </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img alt="" width="100%" src={SlideImage2} />
            <Carousel.Caption>
              <h2 className="text-capitalize">Touch the nature</h2>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img alt="" width="100%" src={SlideImage3} />
            <Carousel.Caption>
              <h2 className="text-capitalize">Build inner power</h2>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img alt="" width="100%" src={SlideImage4} />
            <Carousel.Caption>
              <h2 className="text-capitalize">Stay secured Live smart</h2>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </Row>
    </Grid>
  );
};

export default IntroSlider;



