import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import IntroSlider from './IntroSlider';
import CourseIntro from './CourseIntro';
import CourseTeasers from './CourseTeasers';
import CoachTeasers from './CoachTeasers';
import Banner from './Banner';

class TopPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0)
  }

  render() {
    return (
      <div>
        <IntroSlider />
        <Banner />
        <CourseIntro />
        <CourseTeasers />
        <CoachTeasers titled={true} limit={6} />
      </div>
    );
  }
}

TopPage.route = <Route exact path="/" component={TopPage}/>;

export default TopPage;



