import React from 'react';
import {Grid, Row, Button} from 'react-bootstrap';

const IntroSlider = () => {
  return (
    <div className="banner">
    <Grid>
      <Row>
          <h2>Các khoá học hấp dẫn hè 2017 sắp bắt đầu.</h2>
          <div className="reg-container">
            <Button href="#" bsClass="reg-button text-large">Đăng kí</Button>
          </div>
      </Row>
    </Grid>
  </div>
  );
};

export default IntroSlider;



