import React from 'react';
import {Link} from 'react-router-dom';
import {Grid, Row, Navbar, Nav, NavItem} from 'react-bootstrap';

import Logo from './img/logo.png';

const navItem = (text, href) => (
  <NavItem
    href={href}
    active={window.location.href.endsWith(href)}
    onClick={() => window.location = href}
  >
    {text}
  </NavItem>
);

const Header = () => {
  return (
    <Grid fluid={true}>
      <Row>
        <div id="header">
          <Navbar fluid={true} fixedTop={true} collapseOnSelect>
            <Navbar.Header>
              <Link to="/" className="navbar-left"><img id="logo" alt="PDI Logo" width="86" height="78" src={Logo} /></Link>
              <Navbar.Text id="tagline" className="navbar-left">Đồng hành trên mỗi bước trưởng thành</Navbar.Text>
              <Navbar.Toggle />
            </Navbar.Header>

            <Navbar.Collapse>
              <Nav pullRight>
                {navItem('Trang chủ', '/#/')}
                {navItem('Giới thiệu', '/#/about')}
                {navItem('Khoá học', '/#/courses')}
                {navItem('Sự kiện', '/#/events')}
                {navItem('Chia sẻ', '/#/blog')}
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </div>
      </Row>
    </Grid>
  );
};

export default Header;



