import React from 'react';
import {Row, Col, Grid} from 'react-bootstrap';

const CourseIntro = () => {
  return (
    <div className="course-intro">
      <Grid>
        <Row>
          <Col md={10} mdOffset={1}>
          <h2 className="uppercase title-underline">Giúp trẻ tự tin, phát triển toàn diện</h2>
          <p className="text-center text-large">Chương trình giảng dạy tại PDI được thiết kế riêng cho trẻ em Việt Nam phù hợp và bổ trợ cho hệ thống giáo dục cấp cơ sở, dựa trên nền tảng kết hợp chương trình hướng đạo cho thanh thiếu niên sáng lập bởi Bader Powell và học thuyết Trí thông minh đa dạng của Howard Gardner, thông qua phương pháp học tập qua trải nghiệm.</p>
          <p className="text-center text-large">Chương trình tập trung phát triển các nhóm kỹ năng thiết yếu cho trẻ em ở độ tuổi từ 8 đến 14 tuổi nhằm giúp các em tự tin và tự chủ ứng phó với mọi tình huống gặp phải trong cuộc sống hàng ngày. Chương trình được chia thành ba nhóm kỹ năng chính bao gồm</p>
          </Col>
        </Row>

        <Row className="clearfix space-top">
          <Col md={4} className="text-center">
            <div className="icon"><img alt="first aid" src={`./img/people.svg`} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Nhóm kỹ năng<br/>thực hành xã hội</h2>
              <h4 className="uppercase">Build inner power</h4>
              <p className=" text-md">Hiểu và làm chủ bản thân, tăng khả năng giao tiếp và hòa nhập xã hội, hình thành ý thức trách nhiệm với cộng đồng.</p>
            </div>
          </Col>

          <Col md={4} className="text-center">
            <div className="icon"><img alt="first aid" src={`./img/nature.svg`} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Nhóm kỹ năng<br/> khám phá tự nhiên</h2>
              <h4 className="uppercase">Touch the nature</h4>
              <p className=" text-md">Trang bị cho các em học sinh các kiến thức và kỹ năng cần thiết để các em hiểu biết về các hiện tượng, sự vật trong thiên nhiên, đam mê tìm tòi, khám phá tự nhiên, có ý thức bảo vệ môi trường và có khả năng sinh tồn trong tự nhiên.</p>
            </div>
          </Col>

          <Col md={4} className="text-center">
            <div className="icon"><img alt="first aid" src={`./img/first-aid.svg`} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Nhóm kỹ năng<br/>an toàn</h2>
              <h4 className="uppercase">Stay secured. Live smart</h4>
              <p className=" text-md">Có khả năng ứng biến trong các tình huống nguy cấp, biết cách sơ cấp cứu trong trường hợp cần thiết cũng như bảo vệ bản thân và những người xung quanh khỏi các mối nguy hiểm tiềm tàng trong xã hội hiện đại.</p>
            </div>
          </Col>
        </Row>
      </Grid>
    </div>
  );
};

export default CourseIntro;



