import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col} from 'react-bootstrap';

class AboutPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0)
  }

  render() {
    return (
      <div>
        <div className="parallax first">
          <div className="title-box">
                <span className="white-text"><h2 className="uppercase">Phương pháp giáo dục</h2></span>
          </div>
        </div>

        <div className="intro">
          <Grid>
            <Row>
              <Col md={10} mdOffset={1}>
                <h2 className="text-center">Chương trình Hướng đạo - Scouting Movement</h2>
                <p className="text-center text-large">Bắt đầu từ ý tưởng ấp ủ của một vị tướng người Anh – Baden Powell – chương trình này đã và đang thu hút hàng triệu thanh thiếu nhi trên thế giới. Chương trình Hướng Đạo được xây dựng nhằm khơi nguồn và phát huy mọi khả năng của các em mà không phải dồn ép, đè nén đồng thời hướng tới mục tiêu giúp các em giỏi trên nhiều lĩnh vực khác nhau mà không chỉ là xuất sắc trên một phương diện. Thông qua các hoạt động mới mẻ, các bài giảng độc đáo, chương trình này đã góp phần vào việc giáo dục thanh thiếu niên bằng cách giúp họ phát huy toàn vẹn các kỹ năng về thể chất, xã hội và tinh thần. Qua đó dẫn dắt các em trở thành “những công dân khỏe mạnh, vui vẻ và giúp ích” cho cộng đồng địa phương, cho quốc gia và quốc tế.</p>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h2 className="text-center">Học thuyết Trí thông minh đa dạng - Multiple Intelligences</h2>
                <p className="text-center text-large">Các nghiên cứu của Giáo sư Howard Gardner, một nhà tâm lí học nổi tiếng của Đại học Harvard đã có những ảnh hưởng sâu sắc đến những hoạt động giáo dục trên thế giới. Ông cho rằng có đến 9 loại hình thông minh: Trí thông minh về toán học/logic, trí thông minh về ngôn ngữ/ lời nói, trí thông minh về thị giác/ không gian, trí thông minh về vận động/ thể chất, trí thông minh về âm nhạc/ giai điệu… Quan điểm của ông là chúng ta phải phân tích những hiểu biết về chính bản thân mình để từ đó hiểu chúng ta là ai và chúng ta có thể làm được gì.</p>
              </Col>
            </Row>
            <Row>
                <Col md={10} mdOffset={1}>
                  <h2 className="text-center">Phương pháp học tập qua trải nghiệm - Experiential Learning</h2>
                  <p className="text-center text-large">Học tập qua trải nghiệm giúp học sinh tự tìm tòi, khám phá tri thức cũng như kĩ năng thông qua các hoạt động trải nghiệm thực tế. Người thầy sẽ chỉ đóng vai trò chuyên gia cố vấn, hướng dẫn, hỗ trợ, thiết lập và đánh giá tiêu chuẩn cho việc học và vai trò huấn luyện “coaching”. Sự khác biệt của phương pháp này so với các phương pháp giáo dục mô phạm chính là các bước đúc kết sau quá trình trải nghiệm. Mỗi bước bao gồm các câu hỏi mở được đưa ra để học sinh trả lời, khiến học sinh phải thực sự động não, từ đó tự rút ra bài học cho bản thân. Đây cũng là lúc để đánh giá lại quá trình trải nghiệm của người học. Các câu hỏi rất đa dạng tùy theo từng hoạt động cụ thể. Phương pháp và các bước có thể áp dụng với tất cả các chủ đề, lĩnh vực, tùy theo định hướng của người thiết kế.</p>
                </Col>
              </Row>
          </Grid>
        </div>

        <div className="parallax first">
          <div className="title-box">
                <span className="white-text"><h2 className="uppercase">Triết lý kinh doanh</h2></span>
          </div>
        </div>

        <div className="intro white">
          <Grid>
            <Row>
              <Col md={6} mdOffset={3}>
                <h2 className="text-center">Định hướng chiến lược</h2>
                <h3 className="text-center uppercase">Vision</h3>
                <p className="text-center text-large">PDI mong muốn trở thành tổ chức dẫn đầu về phát triển kỹ năng cá nhân cho học sinh bậc tiểu học và trung học cơ sở</p>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h3 className="text-center uppercase">Mission</h3>
                <p className="text-center text-large">tạo ra một môi trường học tập đa dạng và khác biệt, nơi các học viên có cơ hội trải nghiệm, rèn luyện và trang bị cho bản thân các kiến thức và kỹ năng thiết yếu trong cuộc sống; đồng thời giúp các em hình thành nên những thói quen sống và quan niệm đạo đức tốt đẹp vì tương lai hạnh phúc và thành công của chính các em.</p>
              </Col>
            </Row>
            <Row>
              <Col md={6} mdOffset={3}>
                <h2 className="text-center">Giá trị cốt lõi</h2>
                <h3 className="text-center">Connection – Kết nối cộng đồng</h3>
                <h3 className="text-center">Helpfulness –  Sống có ích</h3>
                <h3 className="text-center">Eco-friendliness – Thân thiện với môi trường</h3>
                <h3 className="text-center">Responsibility – Sống trách nhiệm</h3>
                <h3 className="text-center">Respect others – Tôn trọng</h3>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h2 className="text-center">Triết lý kinh doanh</h2>
                <h3 className="text-center uppercase">Humanity - Nhân văn</h3>
                <p className="text-center text-large">PDI chú trọng phát triển sự nghiệp giáo dục con người, lấy con người làm trung tâm. Đối tượng học viên là các em học sinh từ 8 – 15 tuổi, là lứa tuổi phát triển mạnh mẽ nhất về thể chất lẫn trí tuệ, là lứa tuổi có thể tiếp thu tối đa các kiến thức và kỹ năng, từ đó hình thành nên con người trưởng thành.</p>
                <p className="text-center text-large">PDI tập trung truyền tải cho các em những giá trị tinh thần cốt lõi của cuộc sống. Đó là Yêu thương – Chia sẻ - Đoàn kết – Tôn trọng - Trách nhiệm.</p>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h3 className="text-center uppercase">Advance - Tiến bộ</h3>
                <p className="text-center text-large">PDI áp dụng các phương pháp giảng dạy tiên tiến, sáng tạo để mang đến hiệu quả cao nhất cho đối tượng được giảng dạy. Học viên sẽ liên tục được trải nghiệm, thực hành, trau dồi kỹ năng và được giao lưu trong cộng đồng bao gồm các thành viên của chính PDI.</p>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h3 className="text-center uppercase">Sustainability - Bền vững</h3>
                <p className="text-center text-large">PDI tạo ra sự kết nối thông qua một cộng đồng thực &amp; ảo - nơi các học viên có quyền tự do giao lưu, học hỏi và thể hiện bản thân.</p>
                <p className="text-center text-large">Cùng với gia đình, PDI sẽ theo sát bước đường trưởng thành của các học viên, tạo nền tảng để các em trở thành những cá nhân Tự tin – Tự lập – Sáng tạo – Có trách nhiệm trong xã hội.</p>
              </Col>
            </Row>
          </Grid>
        </div>

        <div className="parallax first">
          <div className="title-box">
                <span className="white-text"><h2 className="uppercase">Our team</h2></span>
          </div>
        </div>

        <div className="intro">
          <Grid>
            <Row>
              <Col md={8} mdOffset={2}>
                <h2 className="text-center white">Thành viên sáng lập</h2>
                <p className="text-center text-large">Với quan niệm “gieo suy nghĩ, gặt hành động; gieo hành động, gặt thói quen; gieo thói quen, gặt tính cách; gieo tính cách, gặt số phận.”, các thành viên sáng lập ra PDI mong muốn đóng góp một phần nhỏ trên bước đường trưởng thành của các em học viên. Thông qua những bài học sáng tạo, những trải nghiệm thú vị, chúng tôi hi vọng sẽ mang tới cho các em những kỷ niệm tuổi thơ đáng nhớ và hành trang vững vàng trong tương lai.</p>
              </Col>
            </Row>
            <Row>
              <Col md={8} mdOffset={2}>
                <h2 className="text-center white">Hội đồng chuyên môn</h2>
                <p className="text-center text-large">Các chương trình giảng dạy của PDI được đánh giá và thông qua bởi hội đồng chuyên môn bao gồm các giáo viên, giảng viên và các nhà tâm lý và nghiên cứu giáo dục giàu kinh nghiệm.</p>
              </Col>
            </Row>

          </Grid>
        </div>

        <div className="parallax less-height">
        </div>

      </div>
    );
  }
}

AboutPage.route = <Route exact path="/about" component={AboutPage}/>;

export default AboutPage;



