import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import courses from './courses.json';

const CourseShow = ({course}) => (
  <div>
    <h2>{course.name}</h2>
    <p>{course.teaser}</p>
    <p>{course.intro}</p>
  </div>
);

class CoursesPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0)
  }

  render() {
    return (
      <div>
        <h1>Các khóa học</h1>

        {courses.map(c => <CourseShow key={c.id} course={c} />)}
      </div>
    );
  }
}

CoursesPage.route = <Route exact path="/courses" component={CoursesPage}/>;

export default CoursesPage;



